// Registration Screen
// Styles the registration index.php form and elements.
// @version 3.0.0

.register-page {

	.register-section {

		@include box-model();
	}

	.signup-form {
		margin-top: $marg-lrg;

		.default-profile {

			input {
				margin-bottom: $marg-lrg;
			}
		}

		label,
		legend {
			margin: $marg-sml 0 0;
		}

		// profile extra element wrapper
		.editfield {
			margin: $marg-med 0;

			fieldset {
				border: 0;
				padding: 0;

				legend {
					margin: 0 0 5px;
					text-indent: 0;
				}
			}

			.field-visibility-settings {
				padding: $pad-sml;

				fieldset {
					margin: 0 0 $marg-sml;
				}
			}
		} // editfield

		#signup-avatar img {
			margin: 0 $marg-med $marg-sml 0;
		}

		.wp-pwd button {
			vertical-align: middle;
		}

		#pass1,
		#pass1-text,
		#pass-strength-result {
			width: 10em;
		}

		#pass1 {
			display: inline-block;
			margin-bottom: inherit;
		}

		#pass1-text,
		.pw-weak {
			display: none;
		}

		.show-password {

			#pass1-text {
				display: inline-block;
				margin-bottom: inherit;
			}

			#pass1 {
				display: none;
			}
		}

		.description.indicator-hint {

			@include font-size(14);
		}

		#submit:disabled {
			color: $light-text;
			opacity: 0.4;
		}

		.password-entry,
		.password-entry-confirm {
			border: 1px solid $bp-border-color;
		}
	} // close .signup-form

} // close .register-page

body.buddypress.register.js {

	.user-pass2-wrap {
		display: none;
	}
}

body.buddypress.register.no-js {

	.wp-hide-pw {
		display: none;
	}
}

// Flex layout containers for registration sections

@include medium-up() {

	.buddypress-wrap {

		.register-page {

			.layout-wrap {
				display: flex;
				flex-flow: row wrap;
				justify-content: space-around;

				.default-profile {
					flex: 1;
					padding-right: $pad-lrg;
				}

				.blog-details {
					flex: 1;
					padding-left: $pad-lrg;
				}
			}

			.submit {
				clear: both;
			}

		}
	}
}

// have we got extended profiles to factor in,
// if so we'll adjust the child item elements.

@include medium-up() {

	.buddypress-wrap.extended-default-reg {

		.register-page {

			.default-profile {
				min-width: 14em;
				flex: 1;
				padding-right: $pad-med;
			}

			.extended-profile {
				flex: 2;
				padding-left: $pad-med;
			}

			.blog-details {
				flex: 1 100%;
			}
		}
	}
}
